import { api } from "api";
import { GET_USER, UPDATE_USER, AUTH_ERROR } from "./types";
import { toast } from "react-toastify";

export const getUser = (userName, callback) => async dispatch => {
  const token = localStorage.getItem("access_token");
  try {
    const response = await api.get(`/users/${userName}/`, {
      params: {
        access_token: token
      }
    });
    localStorage.setItem("user", JSON.stringify(response.data));
    dispatch({ type: GET_USER, payload: response.data });
    callback(response.data);
  } catch (e) {
    dispatch({ type: AUTH_ERROR, payload: "Get User Error" });
  }
};
export const updateUser = (formValues, callback) => async dispatch => {
  toast.info("User update is in progress");
  const token = localStorage.getItem("access_token");
  const user = JSON.parse(localStorage.getItem("user"));
  formValues.userName = user.userName;
  try {
    const response = await api.put("/users/update", formValues, {
      params: {
        access_token: token
      }
    });
    console.log(response.data);
    toast.success("User updated Successfully");
    //dispatch({ type: GET_USER, payload: response.data });
    callback(response.data);
  } catch (e) {
    console.log(e.response);
    toast.error("User updatation failed");
    dispatch({ type: AUTH_ERROR, payload: "User Update Failed" });
  }
};
