import React, { Component } from "react";
import { withStyles } from "@material-ui/core";
import { Switch, Route } from "react-router-dom";
import Container from "@material-ui/core/Container";

import Setting from "components/dashboard/Setting";
import BasicProfile from "components/profile/BasicProfile";
import InputPhone from "components/phoneAuth/InputPhone";
import AppDashboard from "components/dashboard/AppDashboard";

const styles = theme => ({
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4)
  }
});

class Main extends Component {
  render() {
    const { classes } = this.props;
    return (
      <main className="dashboard-view">
        <Container maxWidth="lg" className={classes.container}>
          <Switch>
            <Route exact path="/dashboard" component={AppDashboard} />
            <Route path="/dashboard/profile" component={BasicProfile} />
            <Route path="/dashboard/phoneauth" component={InputPhone} />
            <Route path="/dashboard/setting" component={Setting} />
          </Switch>
        </Container>
      </main>
    );
  }
}

export default withStyles(styles)(Main);
