import React, { Component } from "react";
import Box from "@material-ui/core/Box";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import { withStyles } from "@material-ui/core";
import Paper from "@material-ui/core/Paper";
import Toolbar from "@material-ui/core/Toolbar";
import Grid from "@material-ui/core/Grid";

import { reduxForm } from "redux-form";
import { compose } from "redux";
import { connect } from "react-redux";
import { Field } from "redux-form";
import * as actions from "actions";

import Title from "../Title";
import CountrySelect from "../CountrySelect";

const styles = theme => ({
  form: {},
  fields: {},
  textField: {
    width: "100%"
  },
  registerButton: {
    marginTop: theme.spacing(2),
    width: "100%"
  },
  paper: {
    overflow: "hidden",
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2),
    paddingBottom: theme.spacing(2)
  }
});

class BasicProfile extends Component {
  state = { showOtp: "none" };
  onSubmit = formValues => {
    console.log(formValues);
    this.props.updateUser(formValues, result => {});
  };
  onPinBlur = () => {
    this.setState({ showOtp: "block" });
  };

  renderInput = ({ input, label, type, id, meta, ...custom }) => {
    const showError = meta.error && meta.touched ? true : false;
    return (
      <TextField
        {...input}
        type={type}
        label={label}
        autoComplete={custom.autoComplete}
        required={custom.required}
        variant="outlined"
        className={custom.className}
        size="small"
        margin="normal"
        error={showError}
        helperText={showError ? meta.error : ""}
      />
    );
  };
  render() {
    const { handleSubmit, classes } = this.props;
    return (
      <Grid container spacing={3}>
        <Grid item xs={12} md={6} lg={4}>
          <Paper className={classes.paper} elevation={3}>
            <Toolbar>
              <Title>Basic Profile</Title>
            </Toolbar>
            <form
              onSubmit={handleSubmit(this.onSubmit)}
              className={classes.form}
              noValidate
            >
              <Field
                id="name"
                name="name"
                label="Name"
                type="text"
                component={this.renderInput}
                required={true}
                autoComplete="off"
                className={classes.textField}
              />
              <Field
                id="country"
                name="country"
                label="Country"
                type="text"
                component={CountrySelect}
                required={true}
                autoComplete="off"
                className={classes.textField}
              />
              <Field
                id="pin"
                name="pin"
                label="Pin"
                type="password"
                component={this.renderInput}
                required={true}
                autoComplete="off"
                className={classes.textField}
                onFocus={this.onPinBlur}
              />
              <Box display={this.state.showOtp}>
                otp component will apear here
              </Box>
              <Button
                className={classes.registerButton}
                size="large"
                color="primary"
                variant="contained"
                type="submit"
              >
                Save Profile
              </Button>
            </form>
          </Paper>
        </Grid>
      </Grid>
    );
  }
}

const validate = formValues => {
  const errors = {};
  if (!formValues.name) {
    errors.name = "You must enter a name";
  }

  if (!formValues.pin) {
    errors.pin = "You must enter a pin";
  }
  return errors;
};

function mapStateToProps(state) {
  return {
    currentUser: state.userState.currentUser
  };
}

export default compose(
  withStyles(styles),
  connect(mapStateToProps, actions),
  reduxForm({ form: "basicProfile", validate })
)(BasicProfile);
