import React, { Component } from "react";
//import Countdown from "react-countdown"; Todo - Resend not working

import { withStyles } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import Paper from "@material-ui/core/Paper";
import Toolbar from "@material-ui/core/Toolbar";
import Grid from "@material-ui/core/Grid";
import Divider from "@material-ui/core/Divider";

import { reduxForm } from "redux-form";
import { compose } from "redux";
import { connect } from "react-redux";
import { Field } from "redux-form";
import * as actions from "actions";

import Title from "../Title";

const styles = theme => ({
  textField: {
    width: "100%"
  },
  registerButton: {
    marginTop: theme.spacing(2),
    width: "100%"
  },
  btnGroup: {
    marginRight: theme.spacing(2),
    marginBottom: theme.spacing(2)
  },
  paper: {
    overflow: "hidden",
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2),
    paddingBottom: theme.spacing(2)
  }
});

class InputPhone extends Component {
  state = {
    sendOTP: false,
    phone_number: "",
    currentUser: null,
    otp: "",
    countdown: 20,
    isButtonDisabled: true
  };
  componentDidMount() {
    this.setState({ isButtonDisabled: true });
  }
  onSubmit = formValues => {
    console.log(formValues);
  };
  handlePhoneChange = event => {
    this.setState({ phone_number: event.target.value });
  };
  handleOTPChange = event => {
    this.setState({ otp: event.target.value });
  };
  handleLogin = () => {
    this.setState({ sendOTP: true });
  };
  handleOTPCheck = () => {
    console.log("handle otp check");
  };
  renderInput = ({ input, label, type, id, meta, ...custom }) => {
    const showError = meta.error && meta.touched ? true : false;
    return (
      <TextField
        {...input}
        type={type}
        label={label}
        autoComplete={custom.autoComplete}
        required={custom.required}
        variant="outlined"
        className={custom.className}
        size="small"
        margin="normal"
        error={showError}
        helperText={showError ? meta.error : ""}
      />
    );
  };
  render() {
    const { handleSubmit, classes } = this.props;
    if (this.state.sendOTP === false) {
      return (
        <Grid container spacing={3}>
          <Grid item xs={12} md={6} lg={4}>
            <Paper className={classes.paper} elevation={3}>
              <Toolbar>
                <Title>Phone Update</Title>
              </Toolbar>
              <form onSubmit={handleSubmit(this.onSubmit)} noValidate>
                <Field
                  id="phone"
                  name="phone"
                  label="Phone Number"
                  type="tel"
                  value={this.state.phone_number}
                  onChange={this.handlePhoneChange}
                  component={this.renderInput}
                  required={true}
                  autoComplete="off"
                  className={classes.textField}
                />
                <Button
                  onClick={this.handleLogin.bind(this)}
                  className={classes.registerButton}
                  size="large"
                  color="primary"
                  variant="contained"
                  type="submit"
                >
                  Verify
                </Button>
              </form>
            </Paper>
          </Grid>
        </Grid>
      );
    } else {
      return (
        <Grid container spacing={3}>
          <Grid item xs={12} md={6} lg={4}>
            <Paper className={classes.paper} elevation={3}>
              <Toolbar>
                <Title>Verify your phone number</Title>
              </Toolbar>
              <form onSubmit={handleSubmit(this.onSubmit)} noValidate>
                <Field
                  id="verificationCode"
                  name="verificationCode"
                  label="Verification Code"
                  type="number"
                  value={this.state.otp}
                  onChange={this.handleOTPChange}
                  component={this.renderInput}
                  required={true}
                  autoComplete="off"
                  className={classes.textField}
                />
                <Button
                  onClick={() => this.setState({ sendOTP: false })}
                  size="large"
                  className={classes.btnGroup}
                >
                  Cancel
                </Button>
                <Button
                  onClick={this.handleOTPCheck.bind(this)}
                  size="large"
                  color="primary"
                  variant="contained"
                  type="submit"
                  className={classes.btnGroup}
                >
                  Continue
                </Button>
              </form>
            </Paper>
          </Grid>
        </Grid>
      );
    }
  }
}
const validate = formValues => {
  const errors = {};
  if (!formValues.name) {
    errors.name = "You must enter a name";
  }

  if (!formValues.pin) {
    errors.pin = "You must enter a pin";
  }
  return errors;
};

function mapStateToProps(state) {
  return {
    currentUser: state.userState.currentUser
  };
}

export default compose(
  withStyles(styles),
  connect(mapStateToProps, actions),
  reduxForm({ form: "phoneAuth", validate })
)(InputPhone);
